"use client"

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card'
import { Chrome, Loader2 } from 'lucide-react'
import { ADMIN_PASSKEY } from '@/lib/config/admin'
import { signInEmailPassword, signInWithGoogle } from '@/lib/firebase/auth'
import { getUserRole, setUserRole } from '@/lib/firebase/firestore'
import { useToast } from '@/hooks/use-toast'
import { useAuth } from '@/contexts/AuthContext'

export function AdminWelcomeScreen() {
  const router = useRouter()
  const { toast } = useToast()
  const { user, userData } = useAuth()
  const [passkey, setPasskey] = useState('')
  const [passkeyVerified, setPasskeyVerified] = useState(false)
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  })

  useEffect(() => {
    // Check if passkey was verified in this session
    if (typeof window !== "undefined") {
      const verified = sessionStorage.getItem("adminPasskeyVerified")
      if (verified === "true") {
        setPasskeyVerified(true)
      }
    }

    // If user is already logged in and is admin, redirect to dashboard
    if (user && userData?.role === 'admin') {
      router.push('/admin/dashboard')
    }
  }, [user, userData, router])

  const handlePasskeySubmit = (e: React.FormEvent) => {
    e.preventDefault()
    
    if (passkey !== ADMIN_PASSKEY) {
      toast({
        title: 'Invalid Passkey',
        description: 'The admin passkey is incorrect.',
        variant: 'destructive',
      })
      return
    }

    // Store passkey verification in sessionStorage
    if (typeof window !== "undefined") {
      sessionStorage.setItem("adminPasskeyVerified", "true")
    }
    
    setPasskeyVerified(true)
    toast({
      title: 'Passkey Verified',
      description: 'You can now proceed to log in or sign up.',
    })
  }

  // Show passkey form if not verified
  if (!passkeyVerified) {
    return (
      <main className='flex-col flex justify-center items-center min-h-screen bg-muted/30'>
        <div className='container py-20'>
          <div className='mx-auto max-w-md space-y-6'>
            <div className='text-center space-y-4'>
              <div className='flex items-center justify-center gap-2 mb-4'>
                <div className='flex h-12 w-12 items-center justify-center rounded-md bg-primary'>
                  <span className='font-bold text-primary-foreground text-xl'>GD</span>
                </div>
                <span className='font-bold text-2xl text-foreground'>Grad Drive</span>
              </div>
              <h1 className='font-bold text-3xl md:text-4xl text-balance text-foreground'>
                Admin Login
              </h1>
              <p className='text-muted-foreground text-balance'>
                Access the admin panel to manage your Grad Drive platform
              </p>
            </div>

            <Card className='border-border bg-card shadow-sm'>
              <CardHeader>
                <CardTitle>Enter Admin Passkey</CardTitle>
                <CardDescription>
                  Please enter the admin passkey to continue
                </CardDescription>
              </CardHeader>
              <form onSubmit={handlePasskeySubmit}>
                <CardContent className='space-y-4'>
                  <div className='space-y-2'>
                    <Label htmlFor='passkey'>Admin Passkey</Label>
                    <Input
                      id='passkey'
                      type='password'
                      placeholder='Enter admin passkey'
                      value={passkey}
                      onChange={(e) => setPasskey(e.target.value)}
                      required
                      autoFocus
                    />
                  </div>
                  <Button type='submit' className='w-full' size='lg'>
                    Continue
                  </Button>
                </CardContent>
              </form>
            </Card>

            <div className='text-center text-sm text-muted-foreground'>
              <Link href='/' className='text-accent hover:underline'>
                ← Back to main site
              </Link>
            </div>
          </div>
        </div>
      </main>
    )
  }

  // Show login/signup options after passkey verification
  return (
    <main className='flex-col flex justify-center items-center min-h-screen bg-muted/30'>
      <div className='container py-20'>
        <div className='mx-auto max-w-md space-y-6'>
          <div className='text-center space-y-4'>
            <div className='flex items-center justify-center gap-2 mb-4'>
              <div className='flex h-12 w-12 items-center justify-center rounded-md bg-primary'>
                <span className='font-bold text-primary-foreground text-xl'>GD</span>
              </div>
              <span className='font-bold text-2xl text-foreground'>Grad Drive</span>
            </div>
            <h1 className='font-bold text-3xl md:text-4xl text-balance text-foreground'>
              Admin {isSignIn ? 'Log In' : 'Sign Up'}
            </h1>
            <p className='text-muted-foreground text-balance'>
              {isSignIn 
                ? 'Access the admin panel to manage your Grad Drive platform'
                : 'Create an admin account to get started'}
            </p>
          </div>

          <Card className='border-border bg-card shadow-sm'>
            <CardHeader className='space-y-1'>
              <div className='flex items-center gap-2 mb-2'>
                <Button
                  variant={isSignIn ? 'default' : 'ghost'}
                  size='sm'
                  onClick={() => setIsSignIn(true)}
                  className='flex-1'
                >
                  Log In
                </Button>
                <Button
                  variant={!isSignIn ? 'default' : 'ghost'}
                  size='sm'
                  onClick={() => setIsSignIn(false)}
                  className='flex-1'
                >
                  Sign Up
                </Button>
              </div>
              <CardDescription>
                {isSignIn 
                  ? 'Choose your preferred login method'
                  : 'Create your admin account'}
              </CardDescription>
            </CardHeader>
            <CardContent className='space-y-4'>
              <Link href={isSignIn ? '/admin/login' : '/admin/signup'}>
                <Button className='w-full' size='lg' variant='outline'>
                  <Mail className='mr-2 h-4 w-4' />
                  {isSignIn ? 'Log in' : 'Sign up'} with Email
                </Button>
              </Link>
              <Link href={isSignIn ? '/admin/login' : '/admin/signup'}>
                <Button className='w-full' size='lg' variant='outline'>
                  <Chrome className='mr-2 h-4 w-4' />
                  {isSignIn ? 'Log in' : 'Sign up'} with Google
                </Button>
              </Link>
            </CardContent>
          </Card>

          <div className='text-center text-sm text-muted-foreground'>
            <Link href='/' className='text-accent hover:underline'>
              ← Back to main site
            </Link>
          </div>
        </div>
      </div>
    </main>
  )
}

